import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { BeanValue } from './bean/base-value.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
// import { IsRcord, IsDifferValComp2 } from './../constant/codeValue';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-interest-value',
  templateUrl: './interest-value.component.html',
  styleUrls: ['./interest-value.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class InterestValue implements OnInit {
  beanValue: BeanValue = new BeanValue();
  //table数据 
  list: any = [];
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //传入详情的值
  inValueCode: string;
  //提示信息
  msgs: Message[] = [];
  total: number = 10;
  isRcord: any = [];
  isDifferValComp2: any = [];
  code: any;//获取所有码值
  first: number = 0;//分页控制
  tabMesg: any;//表格中无数据时显示的内容

  constructor(public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.isRcord = this.code['IsRcord'];
    this.isDifferValComp2 = this.code['IsDifferValComp2'];
  }

  ngOnInit() {
    this.beanValue.pageNum = 1;
    this.beanValue.pageSize = 10;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.beanValue.pageNum = 1;
    this.beanValue.pageSize = 10;
    this.first = 0;
    this.query();
  }
  query() {
    this.httpService.queryDimensionMaintValueService(this.beanValue).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }
      this.list = data.resultList
      this.total = data.total;

    }, err => {
      this.tabMesg = tableMessage
    })
  }
  //重置
  newSet() {
    this.beanValue = new BeanValue();
    this.beanValue.pageNum = 1;
    this.beanValue.pageSize = 10;
    // this.beanValue.dmsnlCode='';
    // this.beanValue.dmsnlChiNm='';
    // this.first =0;
    // this.query();
  }
  //新增的回调函数
  addCall(param) {
    this.display = param;
  }

  add(param) {
    this.headerTitle = '计价维度值新增';
    this.router.navigate(['/pages/tzb/valuation/interest-value/interest-value-add'])
  }
  //详情
  detail(param) {
    this.headerTitle = '计价维度值详细信息';
    this.showModel = '2';
    this.display = true;
    this.inValueCode = param;
  }

  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //修改按钮事件
  updateClick(item) {
    this.headerTitle = '修改';
    this.display = true;
    this.showModel = '1';
    let aa = item;
    this.inValueCode = aa;
  }
  //修改的回调
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    }
    this.query();
  }

  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          dmsnlCode: car.dmsnlCode,
          dmsnlValue: car.dmsnlValue,
          id: car.id,
        };
        this.httpService.deleteDimensionMaintValueService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
      }
    });
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.beanValue.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.beanValue.pageNum = currentPage;
    this.first = (currentPage - 1) * this.beanValue.pageSize;
    //调用查询的方法
    this.query();
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
