import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { InterestValue } from "./interest-value.component";
import { InterestValueAddComponent } from "./interest-value-add/interest-value-add.component";
// import {  InterestValueDeleteComponent } from "./interest-value-delete/interest-value-delete.component";

export const routes: Routes = [
    {
        path: '',component: InterestValue,
    },  
    {path:'interest-value-add',component:InterestValueAddComponent},
    // {path:'interest-value-delete',component:InterestValueDeleteComponent},
];



export const InterestValueRouting: ModuleWithProviders = RouterModule.forChild(routes);
