import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { BeanValue } from './../bean/base-value.bean';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
// import { IsDifferValComp2 } from './../../constant/codeValue';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { specialSymbol, specialSymbol2, dmsnlCmprSymbCode } from 'app/@uisftech/common/service/validator';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-interest-value-add',
  templateUrl: './interest-value-add.component.html',
  styleUrls: ['./interest-value-add.component.scss'],
  providers: [Commfunc]
})
export class InterestValueAddComponent implements OnInit, OnChanges {
  //数据
  list: any = [];
  list1: any = [];
  //传入的值
  @Input() inValue: string;
  userform: FormGroup; //校验   
  //提示信息
  msgs: Message[] = [];
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  beanValue: BeanValue = new BeanValue();
  data1: any = [];
  isDifferValComp2: any = [];
  code: any;//获取所有码值
  dmsnlScope: any;
  dmsnlCmprSymbCode: any;
  taskCategoryId: string;
  specialSymbol = specialSymbol;
  specialSymbol2 = specialSymbol2;
  dmsnlCmprSymbCodeNum = dmsnlCmprSymbCode;
  show: boolean = false;
  copeNnm: boolean = false;
  isDisable: boolean = false;
  constructor(
    public httpService: ValuationHttpService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.isDifferValComp2 = this.code['IsDifferValComp2'];
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }

  ngOnInit() {
    this.beanValue = new BeanValue();
    this.userform = this.fb.group({
      'dmsnlChiNm': new FormControl('', Validators.required),
      'dmsnlValue': new FormControl('', [Validators.required, Validators.maxLength(10), specialSymbol]),
      'dmslValDesc': new FormControl('', [Validators.required, Validators.maxLength(50), specialSymbol2]),
      'dmsnlCode': new FormControl(''),
      'dmsnlCmprSymbCode': new FormControl(''),
      'dmsnlScope': new FormControl('', Validators.maxLength(60)),
    });

    this.httpService.queryDimensionMaintListService(this.beanValue).subscribe(data => {
      if (data.resultList) {
        this.list = data.resultList;
      }
    })

  }
  ngOnChanges() {

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //维度范围
  doChanges() {
    if (!this.beanValue.dmsnlCmprSymbCode) {
      this.beanValue.dmsnlScope = ''
    }
  }
  //中文名称编码转汉字传后台
  codeValue(val, arr: any = []) {
    let temp = "";
    if (arr.length > 0) {
      arr.forEach(item => {
        if (item['value'] == val) {
          temp = item['label']
        }
      });
    }
    return temp
  }

  //中文名称转编码在页面显示
  codeVae(val, arr: any = []) {
    let temp = "";
    if (arr.length > 0) {
      arr.forEach(item => {
        if (item['label'] == val) {
          temp = item['value']
        }
      });
    }
    return temp
  }

  //确定
  newAdd() {

    if (this.isDisable) {
      return;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    //校验维度范围值
    if (this.beanValue.dmsnlCmprSymbCode && (this.beanValue.dmsnlScope == '' || this.beanValue.dmsnlScope == undefined)) {
      return
    } else if (this.beanValue.dmsnlCmprSymbCode == '06' || this.beanValue.dmsnlCmprSymbCode == '07' || this.beanValue.dmsnlCmprSymbCode == '08' || this.beanValue.dmsnlCmprSymbCode == '09') {
      if (this.beanValue.dmsnlScope != '') {
        if (this.dmsnlCmprSymbCodeNum(null, this.beanValue.dmsnlScope)) {
          return
        }
      }
    }

    this.isDisable = true;
    //提交服务
    // this.beanValue.dmsnlScope=this.dmsnlCmprSymbCode+this.dmsnlScope+"";
    this.beanValue.dmsnlChiNm = this.codeValue(this.beanValue.dmsnlChiNm, this.list);
    this.beanValue.taskCategoryId = this.taskCategoryId;
    this.httpService.addDimensionMaintValueService(this.beanValue)
      .subscribe(data => {
        //  
        if (data.returnCode.code == "success") {
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          this.beanValue.dmsnlChiNm = this.codeVae(this.beanValue.dmsnlChiNm, this.list);

          setTimeout(() => {
            window.history.go(-1);
          }, 1000)
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          this.beanValue.dmsnlChiNm = this.codeVae(this.beanValue.dmsnlChiNm, this.list);
          this.isDisable = false;
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        this.beanValue.dmsnlChiNm = this.codeVae(this.beanValue.dmsnlChiNm, this.list);
        this.isDisable = false;
      })
  }
  //取消
  cancel() {
    window.history.go(-1);
  }
  //维度编码赋值
  doChose() {
    this.beanValue.dmsnlCode = this.beanValue.dmsnlChiNm
  }
}
